import React from 'react'
import axios from '../../plugins/axios';
import './Cell.scss'
import {Link} from 'react-router-dom'
import querystring from 'query-string'
import propTypes from 'prop-types'

export default class Cell extends React.Component {
    state = {
        cell: []
    }
    constructor(props) {
        super(props)
        axios({
            url: '/api/gift'
        }).then(
            res => this.setState({ cell: res.data.data })
        )
    }
    
    render() {
        let { cell } = this.state
        console.log(this.props)
        console.log(cell, 'cell')

        const {dataName} =this.props;
        return (
            <div className='Cell'>
                <div className="am-card">
                    <div className="am-card-body">
                        {/* åŠ å…¥å¾ªç¯ */}
                        {
                            cell && cell.map(val => (
                                <Link key={val._id} className="am-card-item" to={{pathname:`/detail/${val._id}`,search:querystring.stringify({dataName})}} >
                                    <div className="am-card-minfo">
                                        <div className="am-card-thumb">
                                            {/* å›¾ç‰‡ */}
                                            <img src={val.src} />
                                        </div>
                                        <div className="am-card-content">
                                            <div className="am-card-title">{val.price}</div>
                                            <div className="am-card-subtitle">{val.title}</div>
                                        </div>
                                        <div className="am-list-arrow" aria-hidden="true"><span className="am-icon arrow horizontal"></span></div>
                                    </div>
                                    <div className="am-card-footnote">
                                        <img src={val.src} />
                                        {val.produce}
                                    </div>
                                </Link>
                            ))
                        }

                    </div>
                </div>
            </div>
        )
    }
}

//propså‚æ•°é»˜è®¤æ ·å¼
Cell.defaultProps ={

}
//propå‚æ•°ç±»å‹é™åˆ¶ğŸš«
Cell.propTypes={
    dataName:propTypes.string
}
