import React, { Component } from 'react'
import { Carousel, WingBlank } from 'antd-mobile';
import Cell from '../../components/Cell'

export default class Home extends Component {
    state = {
        banner: ['1', '2', '3'],
        imgHeight: 176,
    }
    constructor(props) {
        super(props)
        Home.axios({
            url: '/api/banner'
        }).then(res => {
            this.setState({
                banner: res.data.data
            })
            // console.log(res.data.data)
        })
    }
    componentDidMount() {
        // console.log()
    }
    render() {
        return (
            <div className="Home">
                <WingBlank>
                    <Carousel
                        infinite
                        // beforeChange={(from, to) => console.log(`slide from ${from} to ${to}`)}
                        // afterChange={index => console.log('slide to', index)}
                        autoplay={true}
                    >
                        {this.state.banner.map(val => (
                            <a
                                key={val}
                                href="http://www.mochangba.top:3000"
                                style={{ display: 'inline-block', width: '100%', height: this.state.imgHeight,position:'relative '}}
                            >
                                <img
                                    src={Home.url + val.banner}
                                    alt=""
                                    style={{ width: '100%', verticalAlign: 'top' }}
                                    onLoad={() => {
                                        // fire window resize event to change height
                                        window.dispatchEvent(new Event('resize'));
                                        this.setState({ imgHeight: 'auto' });
                                    }}
                                />
                                <p className='title' style={{position:'absolute',bottom:'20px',color:'#fff'}}>{val.title}</p>
                                <p className='title_2' style={{position:'absolute',bottom:'0'}}>{val.sub_title}</p>
                            </a>
                        ))}
                    </Carousel>
                </WingBlank>
                <Cell {...this.props} dataName='gift'></Cell>
            </div>
        )
    }
}
