import React, { Component } from 'react'

import { Switch, Route, Redirect,NavLink } from 'react-router-dom'

import './App.scss'
//layoutsç»„ä»¶å¼•ç”¨
import Header from '../Header/Header'
import Footer from '../Footer/Footer'
import Shopcar from '../Shopcar/Shopcar'
import Notice from '../Notice/Notice'
import Loading from '../Loading/Loading'
import NavHead from '../NavHead/NavHead'


//pagesé¡µé¢é›†åˆç»„ä»¶
import Home from '../../pages/Home/Home'
import Goods from '../../pages/Goods/Goods'
import Detail from '../../pages/Detail/Detail'
import Business from '../../pages/Business/Business'
import Reviews from '../../pages/Reviews/Reviews'
import User from '../../pages/User/User'
import Errorpage from '../../pages/Errorpage/Errorpage'


//guard è·¯ç”±å®ˆå«ç»„ä»¶
import Auth from '../../guard/Auth'
//å¼•å…¥å‘å¸ƒè®¢é˜…
import PubSub from 'pubsub-js'


export default class App extends Component {
    state = {
        bHead: false,
        bFoot: false,
        bShopcar: false,
        bNotice: false,
        bLoading: false,
        bNavHead: false
    }
    constructor() {
        super();
        //è®¢é˜…   å‘å¸ƒ åœ¨axios.jsæ–‡ä»¶å†…
        this.token = PubSub.subscribe('VIEW_LODING', (msg, data) => {
            //msg ~~ VIEW_LOADING
            //data ~~ å‘å¸ƒçš„ä¼ é€’è¿‡æ¥çš„æ•°æ®
            console.log('VIEW_LOADING', msg, data);
            this.setState({ bLoding: data })
        })
    }
    componentWillReceiveProps(nextProps) {
        let path = nextProps.location.pathname;
        console.log('app componentWillReceiverops path:', path)
        this.checkPath(path)

    }
    componentWillMount() {
        console.log('componentWillMount App')
    }
    componentDidMount() {
        let path = this.props.location.pathname;
        console.log('app componentDidMount path:', path)
        this.checkPath(path)
    }
    checkPath = (path) => {
        if (/^(\/goods|\/detail)/.test(path)) {
            this.setState({
                bHead: true,
                bFoot: true,
                bShopcar: false,
                bNotice: false,

            })
        }
        if (/^(\/home)/.test(path)) {
            this.setState({
                bHead: true,
                bFoot: false,
                bShopcar: false,
                bNotice: false,
            })
        }
        if (/^(\/user|\/business)/.test(path)) {
            this.setState({
                bHead: false,
                bFoot: true,
                bShopcar: false,
                bNotice: false,
            })
        }

    }
    //antæ’ä»¶å‡½æ•°

    render() {
        let { bHead, bFoot, bNotice, bShopcar, bLoading ,bNavHead} = this.state
        return (
            <div className="App">
                {bHead && <Header />}
                {bLoading && <Loading />}
                
                <Switch>
                    <Route path='/home' component={Home}/>
                    <Route path='/goods' component={Goods} />
                    <Auth path='/user' component={User} />
                    <Route path='/business' component={Business} />
                    <Route path='/detail/:id' component={Detail} />
                    <Route path='/reviews' component={Reviews} />
                    <Redirect exact from='/' to='/goods' />
                    <Route component={Errorpage} />
                </Switch>
                {bNotice ? <Notice /> : null}
                {bShopcar && <Shopcar />}
                {bFoot && <Footer />}

            </div>
        )
    }
    componentWillUnmount() {
        //å–æ¶ˆè®¢é˜…
        PubSub.unsubscribe(this.token)
    }
}
